import React from "react";

import { useDispatch, useSelector } from "react-redux";

import { selectDebugMode, setDebugMode, selectCurrentTemplate, selectCurrentDocument } from "../features/config/configSlice";

export default function Header(props) {
  // console.log("-------> render Header ", props);

  const dispatch = useDispatch();

  const isDebugging = useSelector(selectDebugMode);
  const currentTemplate = useSelector(selectCurrentTemplate);
  const currentDocument = useSelector(selectCurrentDocument);

  return (
    <header className="App-header">
      <h1 title="v20.12.10">DYNAMIC FORM</h1>
      <h2>{currentDocument.Title || currentTemplate.Title1}</h2>
      <div className="debug-toggle">
        <input type="checkbox" name="debug" checked={isDebugging} id="debug" onChange={() => dispatch(setDebugMode(!isDebugging))} />
        <label htmlFor="debug">Debug</label>
      </div>
    </header>
  );
}
