import React, { useState, useEffect } from "react";

import { useSelector, useDispatch } from "react-redux";

import { selectDocumentStatus, selectDebugMode, selectCurrentSectionIndex } from "../features/config/configSlice";
import { selectJson, setJson } from "../features/json/jsonSlice";

import { getControlName, getControlPathById } from "../app/utilities";

import {
  DocumentTableOfContents,
  ListMulticontrol,
  Label,
  Text,
  Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Matrix,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
  SearchableListEntryDialogSMS,
  ButtonAddSMS,
  SearchableSMSListView,
  SearchableListEntryDialogEmail,
  ButtonAddEmail,
  SearchableEmailListView,
  MatrixButton,
  DocumentSummaryGrid,
  ButtonSubmitRegister,
  ButtonCloseout,
  ButtonViewDocument,
  ButtonSendEmail,
  ButtonSendSMS,
} from "./controls";

export default function Board(props) {
  // console.log("-------> render Board ", props);

  const json = useSelector(selectJson);
  const currentSectionIndex = useSelector(selectCurrentSectionIndex);
  const documentStatus = useSelector(selectDocumentStatus);
  const isDebugging = useSelector(selectDebugMode);
  const dispatch = useDispatch();

  const currentSection = json.Sections !== undefined ? json.Sections[currentSectionIndex] : [];
  const currentElements = currentSection.Elements;

  const [controls, setControls] = useState([]);

  const components = {
    DocumentTableOfContents,
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Matrix,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
    SearchableListEntryDialogSMS,
    ButtonAddSMS,
    SearchableSMSListView,
    SearchableListEntryDialogEmail,
    ButtonAddEmail,
    SearchableEmailListView,
    MatrixButton,
    DocumentSummaryGrid,
    ButtonSubmitRegister,
    ButtonCloseout,
    ButtonViewDocument,
    ButtonSendEmail,
    ButtonSendSMS,
  };

  /* Watch for section changes * ---------------------------------------------------------------------------- */
  useEffect(() => {
    // if (Object.keys(json).length > 0) console.log("----> json section updated: ", currentElements);

    if (currentElements !== undefined) {
      const controlsFromElements = currentElements.map((element, index) => {
        const componentName = getControlName(element.Type);
        // console.log(`--->componentName: `, componentName);
        let controlComponent;
        if (components[componentName] !== undefined) {
          const Component = components[componentName];
          controlComponent = <Component key={`${componentName.substring(0, 4).toLowerCase()}-${element.Id || element.ElementId}`} controlJson={element} updateJson={updateJsonHandler} />;
          //   controlComponent = <Component key={element.Id} controlJson={element} updateJson={updateJsonHandler} />;
        }
        return controlComponent;
      });
      setControls(controlsFromElements);
    } else setControls("");
  }, [currentElements]);
  /* --------------------------------------------------------------------------- * Watch for section changes */

  /* Update the JSON * ------------------------------------------------------------------------------------- */
  function updateJsonHandler(pJson) {
    // console.log(" -> updateJsonHandler -> updateJsonHandler: ", updateJsonHandler);
    // if (documentStatus === "loaded") dispatch(setDocumentStatus("modified"));
    const filteredJson = { ...pJson };
    if (filteredJson.ElementId) delete filteredJson.ElementId;

    const controlPath = getControlPathById(json, pJson.Id)[0];
    // console.log(" -> updateJsonHandler -> controlPath: ", controlPath);

    // LOG THE CURRENT UPDATED CONTROL
    // console.log("%c " + pJson.Id + " ", "background:green;color:white;", JSON.stringify(controlPath), filteredJson);

    // RELOCATE IN UTILITIES
    let jsonToUpdate = JSON.parse(JSON.stringify(json));
    switch (controlPath.length) {
      case 2:
        jsonToUpdate.Sections[controlPath[0]].Elements[controlPath[1]] = filteredJson;
        break;
      case 3:
        jsonToUpdate.Sections[controlPath[0]].Elements[controlPath[1]].Elements[controlPath[2]] = filteredJson;
        break;
      case 4:
        jsonToUpdate.Sections[controlPath[0]].Elements[controlPath[1]].Elements[controlPath[2]].Elements[controlPath[3]] = filteredJson;
        break;
      default:
        console.log("----> Control path invalid!");
        break;
    }
    // // ONLY FIST SUBLEVEL
    // jsonToUpdate.Sections[controlPath.section].Elements[controlPath.index] = pJson;
    dispatch(setJson(jsonToUpdate));
  }
  /* ------------------------------------------------------------------------------------ * Update the JSON */

  //   const selectedTemplate = useSelector(selectCurrentTemplate);
  //   console.log(" -> Board -> selectedTemplate: ", selectedTemplate);

  const boardControls =
    documentStatus === "loading" ? (
      <div className="skeleton">
        {Array(14)
          .fill("")
          .map((item, index) => (
            <div key={index} className={`${index % 2 === 1 ? "skeleton-bloc-offset" : ""} skeleton-bloc`}></div>
          ))}
      </div>
    ) : (
      controls
    );

  return (
    <div>
      <div className={`${!isDebugging ? "board" : "board board-debug"}`}>{boardControls}</div>
      {/* {JSON.stringify(currentElements)} */}
    </div>
  );
}
