import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import { selectOpenListId, setOpenListId, selectDebugMode } from "../../features/config/configSlice";

import { addToDocumentExport } from "../../app/utilities";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleUp, faAngleDown, faTimes, faCheck } from "@fortawesome/free-solid-svg-icons";
import { useSpring, animated, useTrail, config } from "react-spring";

function ListMultiple(props) {
  const json = props.controlJson;
  const { Type, Title, Id, Data, SelectedValue = "[]", IsVisible = true } = json;
  const dispatch = useDispatch();

  const debugMode = useSelector(selectDebugMode);

  const [collapsed, setCollapsed] = useState(true);
  const [keyword, setKeyword] = useState("");
  const [filteredList, setFilteredList] = useState([]);
  const [selectedValues, setSelectedValues] = useState(JSON.parse(SelectedValue));

  const openListId = useSelector(selectOpenListId);

  const animatedListProps = useSpring({ height: collapsed ? 0 : 200 });
  // const animatedListProps = useSpring({height: collapsed ? 0 : 200, config: { delay: 2000 }})
  const trailPills = useTrail(selectedValues.length, {
    from: {
      opacity: 0,
      marginLeft: -12,
      marginTop: -6,
      transform: "scaleX(0.6) scaleY(0.8)",
    },
    to: {
      opacity: 1,
      marginLeft: 0,
      marginTop: 0,
      transform: "scaleX(1) scaleY(1)",
    },
    // config: { duration: 1250 },
    config: { mass: 1, tension: 500, friction: 26 },
  });

  // AUTO CLOSE
  useEffect(() => {
    if (!collapsed) dispatch(setOpenListId(Id));
  }, [collapsed, Id, dispatch]);

  useEffect(() => {
    if (openListId !== Id) setCollapsed(true);
  }, [openListId, Id]);

  // FILTER THE LIST ITEMS
  useEffect(() => {
    setFilteredList(Data.filter(item => item.toLowerCase().includes(keyword.toLowerCase())));
  }, [Data, keyword]);

  // TOGGLE THE LIST
  function listClickHandler(pEvent) {
    const target = pEvent.target;
    if (target.className === "input-select" || target.className === "input-select-items") setCollapsed(!collapsed);
    else if (target.className === "remove-item") {
      const targetText = target.parentNode.innerText;
      if (targetText !== "") {
        const newValues = selectedValues.filter(item => item !== targetText);
        setSelectedValues(newValues);
        updateJSON(newValues);
      }
    }
  }
  function listIconClickHandler(pEvent) {
    setCollapsed(!collapsed);
  }

  //SET VALUE
  function listItemClickHandler(pEvent) {
    const currentItem = pEvent.target.id;

    const newValues = selectedValues.findIndex(item => item === currentItem) < 0 ? [...selectedValues, currentItem] : selectedValues.filter(item => item !== currentItem);
    setSelectedValues(newValues);
    updateJSON(newValues);
  }

  function updateJSON(pValue) {
    let valuesToExport = { Type, Id, SelectedValue: JSON.stringify(pValue) };

    const newJson = JSON.parse(JSON.stringify(json));
    if (newJson.Value === pValue) {
      newJson.SelectedValue = "";
      valuesToExport.SelectedValue = "";
      addToDocumentExport(valuesToExport);
    } else {
      newJson.SelectedValue = pValue;
      setCollapsed(true);
      addToDocumentExport(valuesToExport);
    }
    props.updateJson(newJson);
  }

  // DISPLAY THE LIST ITEMS
  const itemList = collapsed
    ? ""
    : filteredList.map((item, key) => {
        const isSelected = selectedValues.findIndex(value => item === value) > -1;
        const iconCheck = isSelected ? <FontAwesomeIcon icon={faCheck} /> : "";
        return (
          <div key={key} id={item} className={`input-list-item ${isSelected ? "selected-list-item" : ""}`} onClick={listItemClickHandler}>
            {iconCheck}
            {item}
          </div>
        );
      });

  // DISPLAY THE SELECTED ITEMS
  const selectedPills = trailPills.map((props, index) => {
    const item = selectedValues[index];
    if (item !== "")
      return (
        <animated.div className="input-select-item" style={props} key={index}>
          {item}
          <button id={item} className="remove-item"></button>
        </animated.div>
      );
    return "";
  });

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>
        <div className="input-select" onClick={listClickHandler}>
          <div className="input-select-items">{selectedPills}</div>
          <FontAwesomeIcon icon={collapsed ? faAngleDown : faAngleUp} onClick={listIconClickHandler} />
        </div>
        <animated.div style={animatedListProps} className="input-list">
          <div className="input-list-searchbar">
            <input type="text" placeholder="Search" className="input-list-search" value={keyword} onChange={e => setKeyword(e.target.value)}></input>
            <FontAwesomeIcon icon={faTimes} onClick={e => setKeyword("")} />
          </div>
          <div className="input-list-items">{itemList}</div>
        </animated.div>
      </div>
    </div>
  ) : (
    ""
  );
}

export default ListMultiple;
