import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleUp, faAngleDown } from "@fortawesome/free-solid-svg-icons";

import { addToDocumentExport } from "../../app/utilities";

function Checkbox(props) {
  const json = props.controlJson;
  const { Type, Title, Id, ShowComment, Collapsible, IsCollapsed, EntryTitle, Details, HasUnderLine, IsVisible = true, Value = "", CommentValue = "" } = json;
  // console.log(`--->: Checkbox -> IsVisible`, IsVisible)

  const [collapsed, setCollapsed] = useState(IsCollapsed);

  const debugMode = useSelector(selectDebugMode);

  function onChangeHandler(pEvent) {
    // setValue(pEvent.target.value)

    const checkboxValue = pEvent.target.checked === true ? "Y" : "N";
    const valuesToExport = { Type, Id, Value: checkboxValue };
    // console.log('---> CHECKBOX UPDATED: ', valuesToExport)
    addToDocumentExport(valuesToExport);

    const newJson = JSON.parse(JSON.stringify(json));
    newJson.Value = checkboxValue;
    // console.log('value: ', newJson.Value, json)
    props.updateJson(newJson);
  }

  function onCommentChangeHandler(pEvent) {
    // console.log('pEvent.target.value: ', pEvent.target.value)

    const commentText = pEvent.target.value;
    const valuesToExport = { Id, CommentValue: commentText };
    // console.log('---> CHECKBOX UPDATED: ', valuesToExport)
    addToDocumentExport(valuesToExport);

    const newJson = JSON.parse(JSON.stringify(json));
    newJson.CommentValue = commentText;
    props.updateJson(newJson);
  }

  useEffect(() => {
    // console.log('Checkbox set data :', json)
  }, [json]);

  function onToggleClickHandler(pEvent) {
    setCollapsed(!collapsed);
  }

  const comment = ShowComment ? (
    <div className="control-checkbox-option">
      <input type="text" placeholder={EntryTitle} value={CommentValue} onChange={onCommentChangeHandler}></input>
    </div>
  ) : (
    ""
  );

  const details = Collapsible ? (
    <div className="control-details-container">
      <div className="control-details-toggle" onClick={onToggleClickHandler}>
        <FontAwesomeIcon icon={collapsed ? faAngleDown : faAngleUp} />
      </div>
      <div id="controlDetails" className={`${collapsed ? "hidden" : ""} control-details-details`}>
        {Details}
      </div>
    </div>
  ) : (
    ""
  );

  const entryTitle = EntryTitle ? (
    <div className="control-checkbox-option">
      <label htmlFor="checkbox">{EntryTitle}</label>
    </div>
  ) : (
    ""
  );

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
      </div>

      <div className={`control ${HasUnderLine ? "control-underline" : ""}`}>
        <div className="control-checkbox">
          <input id={Id} type="checkbox" defaultChecked={Value === "Y"} onChange={onChangeHandler} />
          <label htmlFor={Id}>{Title}</label>
        </div>
        {details}
        {entryTitle}
        {comment}
      </div>
    </div>
  ) : (
    ""
  );
}

export default Checkbox;
