import React from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

function Label(props) {
  const json = props.controlJson;
  const { Type, Title, Id, IsVisible = true, FontAttributes, FontSize = 14, Margin = 0, HasUnderLine = false } = json;

  const debugMode = useSelector(selectDebugMode);

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
      </div>
      <div className="control">
        <div
          // className="control"
          style={{
            fontSize: FontSize - 2 + "px",
            fontWeight: FontAttributes === "Bold" ? "bold" : "",
            fontStyle: FontAttributes === "Italic" ? "italic" : "",
            borderBottom: HasUnderLine ? "1px solid black" : "",
            padding: Margin ? Margin + "px" : "",
          }}
        >
          {Title}
        </div>
      </div>
    </div>
  ) : (
    ""
  );
}

export default Label;
