import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import Modal from "../Modal";

import { selectDebugMode, setCurrentContact, selectCurrentContact, selectDocumentStatus } from "../../features/config/configSlice";
import { addToDocumentExport } from "../../app/utilities";

function Text(props) {
  const json = props.controlJson;

  let { Type, Title, Id, IsVisible = true, IsEnabled = true, Value = "", Format, Warning = false, WarningMessage, Mandatory, ValidationMessage, AssociatedId } = json;
  // Id case insensitive
  if (Boolean(json.ID)) {
    Id = json.ID;
  }

  const debugMode = useSelector(selectDebugMode);
  const currentContact = useSelector(selectCurrentContact);
  // const status = useSelector(selectDocumentStatus)
  const dispatch = useDispatch();

  const [isModalVisible, setIsModalVisible] = useState(false);
  const [modalOptions, setModalOptions] = useState(Mandatory ? { message: ValidationMessage, bgColor: "#EF643C" } : { message: WarningMessage, bgColor: "#000000" });

  const emailAddressTester = /^[-!#$%&'*+\/0-9=?A-Z^_a-z`{|}~](\.?[-!#$%&'*+\/0-9=?A-Z^_a-z`{|}~])*@[a-zA-Z0-9](-*\.?[a-zA-Z0-9])*\.[a-zA-Z](-?[a-zA-Z0-9])+$/;
  const phoneNumberRestrictCodes = [49, 50, 51, 52, 53, 54, 55, 56, 57, 48, 189, 16, 57, 48, 16, 187, 32, 8, 35];

  // useEffect(() => {
  //   if (status === 'reset-currentContact') {
  //     const newJson = JSON.parse(JSON.stringify(json))
  //     newJson.Value = ''
  //     console.log(`---> ~ file: Text.js ~ line 36 ~ useEffect ~ newJson`, newJson)
  //     props.updateJson(newJson)

  //     // console.log('---> Should reset this: ', document.getElementById('ManualSMSName').value, document.getElementById('ManualSMSPhone').value)
  //     // document.getElementById('ManualSMSName').value = ''
  //     // document.getElementById('ManualSMSPhone').value = ''
  //   }
  // }, [status])

  function onChangeHandler(pEvent) {
    // EMAIL&SMS Register
    if (AssociatedId && AssociatedId.Names.length > 0) {
      if (Id === "ManualSMSName") dispatch(setCurrentContact({ ...currentContact, name: pEvent.target.value }));
      if (Id === "ManualSMSPhone")
        dispatch(
          setCurrentContact({
            ...currentContact,
            phoneNumber: pEvent.target.value,
          })
        );
      if (Id === "ManualEmailName") dispatch(setCurrentContact({ ...currentContact, name: pEvent.target.value }));
      if (Id === "ManualEmailEmail") dispatch(setCurrentContact({ ...currentContact, email: pEvent.target.value }));
    }

    const valuesToExport = { Type, Id, Value: pEvent.target.value };
    addToDocumentExport(valuesToExport);

    const newJson = JSON.parse(JSON.stringify(json));
    newJson.Value = pEvent.target.value;
    // console.log('value: ', json)
    props.updateJson(newJson);
  }

  function onBlurHandler(pEvent) {
    const target = pEvent.target;

    if (Warning && target.value === "") {
      setIsModalVisible(true);
    }
    if (Mandatory && target.value === "") {
      target.focus();
      setIsModalVisible(true);
    }
    if (Format !== undefined && Format.Keyboard === "Email") {
      if (!emailAddressTester.test(pEvent.target.value.toLowerCase())) {
        setModalOptions({
          message: "The email address provided appears not to be valid",
          bgColor: "#000000",
        });
        setIsModalVisible(true);
      }
    }
  }

  function onKeyDownHandler(pEvent) {
    // console.log('---> ', pEvent.keyCode)
    if (Format.Keyboard === "Telephone") {
      if (!phoneNumberRestrictCodes.includes(pEvent.keyCode)) pEvent.preventDefault();
    }
  }

  useEffect(() => {
    // console.log('Text set data: ', Value)
  }, [json]);

  const textfield =
    Format !== undefined ? (
      Format.Lines !== undefined ? (
        <textarea id={Id} rows={Format.Lines} onChange={onChangeHandler} defaultValue={Value}></textarea>
      ) : Format.Keyboard === "Numeric" ? (
        <input type="number" min={Format.Min} max={Format.Max} size={Format.Decimal} onChange={onChangeHandler} />
      ) : Format.Keyboard === "Telephone" ? (
        <input type="text" maxLength="20" onKeyDown={onKeyDownHandler} onChange={onChangeHandler} />
      ) : (
        <input id={Id} type="text" value={Value} maxLength={Format.VarChar || -1} disabled={!IsEnabled} onBlur={onBlurHandler} onChange={onChangeHandler} />
      )
    ) : (
      <input id={Id} type="text" value={Value} disabled={!IsEnabled} onBlur={onBlurHandler} onChange={onChangeHandler} />
    );

  const warningMarker = WarningMessage ? "*" : "";
  const mandatoryMarker = Mandatory ? <span className="warning-red">*</span> : "";

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
      </div>
      <div className="control">
        <label htmlFor="title">
          {Title} {warningMarker}
          {mandatoryMarker}
        </label>
        {textfield}
      </div>
      <Modal
        visibility={isModalVisible}
        toggleVisibility={() => setIsModalVisible(!isModalVisible)}
        message={`${Id}: ${modalOptions.message}`}
        css={{ backgroundColor: modalOptions.bgColor }}
        backdrop={"rgba(0,0,0,0.3)"}
      />
    </div>
  ) : (
    ""
  );
}

export default Text;
