import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { selectCurrentSectionIndex, callSection, prevSection, nextSection } from "../features/config/configSlice";
import { selectJson } from "../features/json/jsonSlice";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faStepBackward, faStepForward, faFastBackward, faFastForward } from "@fortawesome/free-solid-svg-icons";
import styles from "./Pagination.module.scss";

function Pagination(props) {
  // console.log("-------> render the Pagination", props);

  const dispatch = useDispatch();
  const json = useSelector(selectJson);

  const sectionsAmount = json.Sections !== undefined ? json.Sections.length : 0;
  const currentSectionIndex = useSelector(selectCurrentSectionIndex);

  const tabLength = json.Sections ? json.Sections.length : 0;
  const tabTitle = json.Sections ? json.Sections[currentSectionIndex].Title : "";

  function onFirstSectionHandler(e) {
    dispatch(callSection(0));
  }
  function onPrevSectionHandler(e) {
    if (currentSectionIndex > 0) dispatch(prevSection());
  }
  function onNextSectionHandler(e) {
    if (currentSectionIndex < tabLength - 1) dispatch(nextSection());
  }
  function onLastSectionHandler(e) {
    dispatch(callSection(tabLength - 1));
  }

  return (
    <nav>
      <div className={styles.row}>
        <button className="btn btn-primary" disabled={tabLength === 0 || currentSectionIndex === 0} onClick={e => onFirstSectionHandler(e)}>
          <FontAwesomeIcon icon={faFastBackward} />
        </button>
        <button className="btn btn-primary" disabled={tabLength === 0 || currentSectionIndex === 0} onClick={e => onPrevSectionHandler(e)}>
          <FontAwesomeIcon icon={faStepBackward} />
        </button>
        <div className={styles.title}>
          <h2>{tabTitle}</h2>
        </div>
        <button className="btn btn-primary" disabled={tabLength === 0 || currentSectionIndex === tabLength - 1} onClick={e => onNextSectionHandler(e)}>
          <FontAwesomeIcon icon={faStepForward} />
        </button>
        <button className="btn btn-primary" disabled={tabLength === 0 || currentSectionIndex === tabLength - 1} onClick={e => onLastSectionHandler(e)}>
          <FontAwesomeIcon icon={faFastForward} />
        </button>
      </div>
      <div>
        <div className="currentSectionIndicator">
          {currentSectionIndex + 1} of {sectionsAmount}
        </div>
      </div>
    </nav>
  );
}

export default Pagination;
