export function getControlPathById(pJson, pElementId) {
  const currentJson = normalizeJson(pJson).Sections;
  // console.log(`--> Looking for pElementId: `, pElementId, " inside pJson: ", currentJson);

  // console.log(`--->: getElementFromElementId inside this: `, currentJson);
  let foundPath = [];

  if (pElementId !== "undefined") {
    currentJson.forEach((section, sectionIndex) => {
      section.Elements.forEach((item, index) => {
        if (item.hasOwnProperty("Elements")) {
          item.Elements.forEach((subitem, subindex) => {
            if (subitem.hasOwnProperty("Elements")) {
              subitem.Elements.forEach((subsubitem, subsubindex) => {
                if (subsubitem.Id !== undefined && subsubitem.Id === pElementId) {
                  // console.log("--> subsubpath: ", { sectionIndex, index, subindex, subsubindex });
                  foundPath.push([sectionIndex, index, subindex, subsubindex]);
                }
              });
            }

            if (subitem.Id !== undefined && subitem.Id === pElementId) {
              // console.log("--> subpath: ", { sectionIndex, index, subindex });
              foundPath.push([sectionIndex, index, subindex]);
            }
          });
        }

        if (item.Id !== undefined && item.Id === pElementId) {
          // console.log("--> path: ", { sectionIndex, index });
          foundPath.push([sectionIndex, index]);
        }
      });
    });
  }

  // console.log(" -> getElementFromElementId -> foundPath: ", foundPath);
  return foundPath;
}

