import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";
import { setJson, selectJson, setRepeaterValue, selectRepeaterValue } from "../../features/json/jsonSlice";

import { getControlName } from "../../app/utilities";
import {
  DocumentTableOfContents,
  ListMulticontrol,
  Label,
  Text,
  // Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Matrix,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
  SearchableListEntryDialogSMS,
  ButtonAddSMS,
  SearchableSMSListView,
  SearchableListEntryDialogEmail,
  ButtonAddEmail,
  SearchableEmailListView,
  MatrixButton,
  DocumentSummaryGrid,
  ButtonSubmitRegister,
  ButtonCloseout,
  ButtonViewDocument,
} from "./";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlusSquare, faTrash } from "@fortawesome/free-solid-svg-icons";

function Repeatable(props) {
  const json = props.controlJson;
  const { Type, Title, Id, Elements, IsVisible = true } = json;
  // console.log(`--->: New repeatable -> json: `, json.Id, json.Elements);

  const dispatch = useDispatch();

  const repeaterValue = useSelector(selectRepeaterValue);
  const debugMode = useSelector(selectDebugMode);

  const currentRepreaterIndex = repeaterValue.findIndex(repeater => repeater.Id === Id);
  const repeatableSchema = Elements.map(control => {
    // if (control.Value) control.Value = "";
    const elementCopy = { ...control };
    delete elementCopy.ElementId;
    return elementCopy;
  });
  // console.log(" -> Repeatable -> repeatableSchema: ", repeatableSchema);
  // console.log(`--->: Repeatable -> repeaterValue`, repeaterValue)

  const components = {
    DocumentTableOfContents,
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Matrix,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
    SearchableListEntryDialogSMS,
    ButtonAddSMS,
    SearchableSMSListView,
    SearchableListEntryDialogEmail,
    ButtonAddEmail,
    SearchableEmailListView,
    MatrixButton,
    DocumentSummaryGrid,
    ButtonSubmitRegister,
    ButtonCloseout,
    ButtonViewDocument,
  };

  // useEffect(() => {
  //   console.log("----> repeaterValue: ", repeaterValue);
  //   console.log("----> found values: ", repeaterValue[Id]);
  // }, [repeaterValue, Id]);

  function updateJsonHandler(pJson, pRepeaterIndex) {
    // // console.log('this repeater: ', pRepeaterIndex)
    // // console.log('update this: ', pJson)
    // const repeaterControlIndex = repeaterValue[pRepeaterIndex].findIndex(item => item.Id === pJson.Id);
    // const newRepeaterValues = [...repeaterValue];
    // console.log(`--->: updateJsonHandler -> newRepeaterValues`, newRepeaterValues);
    // console.log(`--->: updateJsonHandler -> newRepeaterValues[pRepeaterIndex][repeaterControlIndex]`, newRepeaterValues[pRepeaterIndex][repeaterControlIndex]);
    // newRepeaterValues[pRepeaterIndex] = [...newRepeaterValues[pRepeaterIndex], pJson];
    // dispatch(setRepeaterValue(newRepeaterValues));
    // // props.updateJson({ ...json, Elements: mergedElements })
  }

  function addRepeatable() {
    const repeaterValueToUpdate = JSON.parse(JSON.stringify(repeaterValue));

    const repeaterIndex = currentRepreaterIndex < 0 ? repeaterValue.length : currentRepreaterIndex;
    if (currentRepreaterIndex < 0) {
      repeaterValueToUpdate.push({ Type: "Repeatable", RepeaterValue: [repeatableSchema], Id });
    } else repeaterValueToUpdate[repeaterIndex].RepeaterValue.push(repeatableSchema);
    dispatch(setRepeaterValue(repeaterValueToUpdate));
  }

  function removeRepeatableByIndex(pIndex) {
    console.log("----> repeaterValue: ", repeaterValue);

    const repeaterValueToUpdate = JSON.parse(JSON.stringify(repeaterValue));

    const repeatableIndex = repeaterValue.findIndex(repeatable => repeatable.Id === Id);
    console.log(" -> removeRepeatableByIndex -> pIndex", pIndex);

    const repeatableValue = repeaterValue[repeatableIndex].RepeaterValue.filter((repeat, index) => index !== pIndex);
    console.log(" -> removeRepeatableByIndex -> repeatableValue", repeatableValue);
    repeaterValueToUpdate[0].RepeaterValue = repeatableValue;

    console.log("----> repeaterValueToUpdate: ", repeaterValueToUpdate);
    dispatch(setRepeaterValue(repeaterValueToUpdate));
  }

  const repeatable = (pControls, pIndex) => (
    <div className="repeatable" key={`rep-${pIndex}`}>
      <div className="repeatable-header">
        <h3 className="repeatable-title">
          #{pIndex + 1}, {Title}
        </h3>
        <button className="btn btn-secondary btn-small" onClick={e => removeRepeatableByIndex(pIndex)}>
          Remove
          <FontAwesomeIcon icon={faTrash} />
        </button>
      </div>
      {pControls}
    </div>
  );

  let children;

  if (currentRepreaterIndex < 0) {
    console.log("----> repeatable is empty, process with repeatableSchema: ", repeatableSchema);
    const controls = repeatableSchema.map((control, index) => {
      const componentName = getControlName(control.Type);
      const Component = components[componentName];
      return <Component key={index} controlJson={{ ...control }} updateJson={pJson => updateJsonHandler(pJson, index)} />;
    });
    children = repeatable(controls, 0);
  } else {
    // console.log(" -> Repeatable -> currentRepreater", repeaterValue[currentRepreaterIndex].RepeaterValue);
    children = repeaterValue[currentRepreaterIndex].RepeaterValue.map((values, index) => {
      // console.log("---> repeaterValue: ", values);
      const controls = values.map((control, index) => {
        const controlBaseJson = repeatableSchema.filter(item => item.Id === control.Id)[0];
        // console.log(" -> controls -> controlBaseJson", controlBaseJson);

        const componentName = getControlName(control.Type);
        const Component = components[componentName];
        return <Component key={index} controlJson={{ ...control, ...controlBaseJson }} updateJson={pJson => updateJsonHandler(pJson, index)} />;
      });
      return repeatable(controls, index);
    });
  }
  console.log(" -> children", children);

  // const children = repeaterValue.map((value, repeaterIndex) => {
  //   console.log(`--->value: `, value);

  //   const controls = Elements.map((control, index) => {
  //     // console.log('---> ', control)

  //     value.forEach((repeaterControl, index) => {
  //       if (control.Id === repeaterControl.Id) control = { control, ...repeaterControl };
  //     });

  //     const componentName = getControlName(control.Type);
  //     const Component = components[componentName];
  //     return <Component key={index} controlJson={control} updateJson={pJson => updateJsonHandler(pJson, repeaterIndex)} />;
  //   });

  //   return (
  //     <div className="repeatable" key={`rep-${repeaterIndex}`}>
  //       <div className="repeatable-header">
  //         <h3 className="repeatable-title">
  //           #{repeaterIndex + 1}, {Title}
  //         </h3>
  //         <button className="btn btn-secondary btn-small" onClick={e => removeRepeatableByIndex(repeaterIndex)}>
  //           Remove
  //           <FontAwesomeIcon icon={faTimes} />
  //         </button>
  //       </div>
  //       {controls}
  //     </div>
  //   );
  // });

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea name="" cols="10" rows="5" value={JSON.stringify(json)} disabled></textarea>
      </div>

      <div className="control">
        {/* <div className="repeatable-header">
            <h3 className="repeatable-title">{Title}</h3>
            <button className="btn btn-secondary btn-small" onClick={e => console.log('ok')}>
              Remove
              <FontAwesomeIcon icon={faTimes} />
            </button>
          </div> */}
        {children}

        <button className="btn btn-primary" onClick={addRepeatable}>
          <FontAwesomeIcon icon={faPlusSquare} />
        </button>
      </div>
    </div>
  ) : (
    ""
  );
}

export default Repeatable;
